body {
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 16px;
	line-height: 21px;
	color: #646464;
	font-weight: normal;
}

.btn {
	display: inline-block;
	cursor: pointer;
	text-align: center;
	vertical-align: top;
	text-decoration: none;
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 16px;
	line-height: 21px;
	color: white;
	font-weight: normal;
	background: linear-gradient(#feab16, #ff7500) #ff9000;
	padding: 10px 30px;
	border-radius: 4px;
	border: 2px solid #ff9000;
	transition: all 0.3s ease;
	box-shadow: 0px 0px 3px 1px #D2D2D2;
}

.btn:hover {
	background: #ff9451;
}

.btn:active {
	background: #f5843e;
}

.btn--call {
	float: right;
	max-width: 210px;
}

.btn--features {
	min-width: 155px;
}

.btn--houses {
	min-width: 155px;
	margin-left: 60px;
}

.btn--form-submit {
	width: 390px;
	box-sizing: border-box;
	height: 55px;
	font-size: 20px;
	text-shadow: -1px 1px 1px #444;
}

.btn--footer {
	width: 100%;
	display: block;
	text-shadow: -1px 1px 1px #444;
	font-size: 20px;
	height: 55px;
	box-shadow: 0 0 3px 1px #787575;
}

.btn--popup {
	width: 280px;
	box-sizing: border-box;
	display: inline-block;
	transition: none;
}

.btn-link {
	font-size: 15px;
	color: #029dff;
	display: block;
	margin-top: 4px;
	font-size: 17px;
}

.btn-link--footer {
	font-size: 18px;
	display: inline-block;
}

input[type="text"],
textarea,
select,
input[type="password"] {
	border: none;
	outline: none;
	background: white;
	box-shadow: 1px -1px 1px 0px #eceaea;
	border: 1px solid #f1f2f6;
	box-sizing: border-box;
	width: 100%;
	height: 55px;
	color: #646464;
	border-radius: 4px;
	padding: 5px 30px;
}

textarea:hover,
textarea:focus,
input[type="text"]:hover,
input[type="text"]:focus {
	border: 1px solid #b7b7b7;
}

label {
	cursor: pointer;
}

button {
	outline: none;
	cursor: pointer;
}

input::-webkit-input-placeholder {
	opacity: 0.8;
}

input::-moz-placeholde {
	opacity: 0.8;
}

select {
	appearance: none;
	background: white url("../img/arrow-select.png") no-repeat right;
	cursor: pointer;
	padding-right: 15px;
	padding-left: 15px;
}

textarea {
	width: 100%;
	height: 205px;
	resize: none;
	padding-top: 15px;
}

input[type="hidden"] {
	width: 1px;
	height: 1px;
	opacity: 0;
}

.overlay {
	background-color: black; /*   */
	opacity: 0; /*   */
	position: fixed; /*   */
	z-index: 9980;
	right: 0; /*  0 */
	top: 0; /*  0 */
	left: 0; /*  0 */
	bottom: 0; /*  0 */
	visibility: hidden; /*   */ /* transition  */
	transition: 0.5s ease;
}

.overlay:target {
	visibility: visible; /*   */
	opacity: 0.8; /*     */
}

.popup {
	position: fixed; /*   */
	visibility: hidden; /*  -  */
	z-index: 9999; /* z-index */
	transform: translateX(-50%);
	left: 50%;
	top: 20px;
	width: 370px;
	padding: 25px 45px;
	background-color: #f4f4f4; /*  */
	border-radius: 7px;
}

.overlay:target + .popup {
	opacity: 1; /*   */
	visibility: visible; /*   */
}

.popup-close {
	display: none;
	width: 3px;
	height: 20px;
	background: red;
	position: absolute;
	top: 7px;
	right: 15px;
	font-size: 0px;
	transform: rotate(45deg);
}

.popup-close:after {
	content: "";
	display: block;
	transform: rotate(-90deg);
	width: 3px;
	height: 20px;
	background: red;
	position: absolute;
	top: 0;
}

.popup__logo {
	margin: 10px auto 30px;
}

.popup__logo img {
	display: block;
	margin: 0 auto;
}

.popup__title {
	font-size: 25px;
	line-height: 31px;
	color: #6a6a6a;
	text-align: center;
	margin-bottom: 20px;
}

.popup__description {
	margin-bottom: 30px;
}

.popup__form-line {
	margin-bottom: 15px;
}

.popup__btn-line {
	text-align: center;
	margin: 20px 0;
}

.popup__footer {
	color: #515151;
	letter-spacing: 1px;
	font-size: 17px;
	text-align: center;
	padding: 0 40px;
}

