.overlay {
	background-color: black; /*   */
	opacity: 0; /*   */
	position: fixed; /*   */
	z-index: 9980;
	right: 0; /*  0 */
	top: 0; /*  0 */
	left: 0; /*  0 */
	bottom: 0; /*  0 */
	visibility: hidden; /*   */ /* transition  */
	transition: 0.5s ease;
}

.overlay:target {
	visibility: visible; /*   */
	opacity: 0.8; /*     */
}

.qualification-slider__big-images {
	position: fixed; /*   */
	visibility: hidden; /*  -  */
	z-index: 9999; /* z-index */
	transform: translateX(-50%);
	left: 45%;
	top: 0px;
}

.qualification-slider__big-images img {
	display: block;
	height: 600px;
	width: 576px;
}

.overlay:target + .qualification-slider__big-images {
	opacity: 1; /*   */
	visibility: visible; /*   */
}

